import accessData from "@/views/home/managerSystemIndex/common/accessData.vue";
import userAccessLog from "@/views/home/managerSystemIndex/common/userAccessLog.vue";
export default {
  data: function data() {
    return {
      indexHeight: 0
    };
  },
  components: {
    accessData: accessData,
    userAccessLog: userAccessLog
  },
  mounted: function mounted() {
    var _this = this;

    this.userAccessEcharts();
    this.listEcharts();

    window.onresize = function () {
      _this.$nextTick(function () {
        _this.indexHeight = document.body.clientHeight - 157 + "px";
      });
    };

    this.$nextTick(function () {
      _this.indexHeight = document.body.clientHeight - 157 + "px";
    });
  },
  methods: {
    userAccessEcharts: function userAccessEcharts() {
      var userAccessEcharts = this.$echarts.init(document.getElementById("userAccessEcharts"));
      userAccessEcharts.setOption({
        title: {
          text: "总体成绩",
          subtext: "纯属虚构",
          left: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "left",
          data: ["较差", "一般", "中等", "良好", "优秀"]
        },
        series: [{
          name: "成绩及数据率",
          type: "pie",
          radius: "55%",
          center: ["50%", "60%"],
          data: [{
            value: 335,
            name: "较差"
          }, {
            value: 310,
            name: "一般"
          }, {
            value: 234,
            name: "中等"
          }, {
            value: 135,
            name: "良好"
          }, {
            value: 1548,
            name: "优秀"
          }],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        }]
      });
    },
    listEcharts: function listEcharts() {
      var listEcharts = this.$echarts.init(document.getElementById("listEcharts"));
      listEcharts.setOption({
        legend: {},
        tooltip: {},
        dataset: {
          source: [["product", "2015", "2016", "2017"], ["考试及格率", 43.3, 85.8, 93.7], ["提交率", 83.1, 73.4, 55.1], ["访问率", 86.4, 65.2, 82.5]]
        },
        xAxis: {
          type: "category"
        },
        yAxis: {},
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [{
          type: "bar"
        }, {
          type: "bar"
        }, {
          type: "bar"
        }]
      });
    }
  }
};